@import url(https://fonts.googleapis.com/css?family=Do+Hyeon);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.min.css);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: linear-gradient(to left, #ffdebe 0%, #ffefdf 100%);
}


::-webkit-input-placeholder {
	color: #333300;
	font-weight: 100;
	font-family: 'FontAwesome', 'Do Hyeon', sans-serif;
	font-size: 26px;
}

:-ms-input-placeholder {
	color: #333300;
	font-weight: 100;
	font-family: 'FontAwesome', 'Do Hyeon', sans-serif;
	font-size: 26px;
}

::placeholder {
	color: #333300;
	font-weight: 100;
	font-family: 'FontAwesome', 'Do Hyeon', sans-serif;
	font-size: 26px;
}

@font-face {
font-family: 'SEGA LOGO FONT';
font-style: normal;
font-weight: normal;
src: local('SEGA LOGO FONT'), url(/Cat-Profiles/static/media/SEGA.47b4bfa9.woff) format('woff');
}

h1{
	font-family: 'SEGA LOGO FONT';
	font-weight: 500;
	color: #668cff;
}
